/**
 * wait
 * 
 * @param {Function} func  
 * @param {Number} wait 
 * @param {Boolean} immediate  
 * @return null
 */
let timeout = null;

const debounce = (func, wait = 500, data, immediate = false) => {
	// 
	if (timeout !== null) clearTimeout(timeout);
	// 
	if (immediate) {
		var callNow = !timeout;
		timeout = setTimeout(() => {
			timeout = null;
		}, wait);
		if (callNow) typeof func === 'function' && func(data);
	} else {
		// timeoutwaitfunc
		timeout = setTimeout(() => {
			typeof func === 'function' && func(data);
		}, wait);
	}
}



/**
 * 
 * 
 * @param {Function} func  
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */
let timer, flag;
const throttle = (func, wait = 500, data, immediate = true) => {
	if (immediate) {
		if (!flag) {
			flag = true;
			// wait
			typeof func === 'function' && func();
			timer = setTimeout(() => {
				flag = false;
			}, wait);
		}
	} else {
		if (!flag) {
			flag = true
			// wait
			timer = setTimeout(() => {
				flag = false
				typeof func === 'function' && func();
			}, wait);
		}

	}
};

// 
const $storage = {
	// 
	setStorage:(key,value) => {
		uni.setStorage({
			key: key,
			data: value
		});
	},
	// 
	getStorage:(key) => {
		let value = uni.getStorageSync(key);
		if(value){
			if(Object.keys(value).length==0) return null
			return value
		}else{
			return null
		}
	},
	// key
	removeStorage:(key)=>{
		uni.removeStorage({
			key: key,
		});
	},
	// 
	clearStorage:()=>{
		uni.clearStorage();
	}
}

// code
const getWxCode = () => {
	const promise = new Promise((resolve,reject)=>{
		uni.login({
			onlyAuthorize:true,
			provider: 'weixin',
			success: loginRes => {
				console.log('loginRes',loginRes)
				if(loginRes.errMsg == 'login:ok'){
					resolve(loginRes.code)
				}else{
					console.log('code-');
				}
			}
		})
	})
	return promise
}


module.exports = {
	debounce,
	throttle,
	$storage,
	getWxCode
}
