//
import request from "@/uni_modules/zhouWei-request/js_sdk/request/index.js";


//
import $config from "./config.js"
//API
import $api from "./api.js"
// VUEX
import $store from '@/store/index.js'
// utils
// import $utils from '../utils/utils.js'
// 
import {$storage, getWxCode} from '@/utils/utils.js'

//newrequest
let $http = new request({
	//
	baseUrl: $config.apiBaseUrl,
	//
	fileUrl: "",
	// url
	defaultUploadUrl: "",
	//content-type
	header: $config.postHeaderDefault,
	// 6000
	timeout: 12000,
	// 
	config: {
		// 
		isPrompt: true,
		// 
		load: false,
		// 
		isFactory: true,
		//
		isSign:true
	}
});

// token
$http.getQnToken = function(callback){
    //
    $http.get("api/common/v1/qn_upload").then(data => {
        /*
         *
         *visitPrefix:
         *token:token
         *folderPath:
         *region:  SCN
         */
        callback({
            visitPrefix: data.visitPrefix,
            token: data.token,
            folderPath: data.folderPath
        });
    });
}
//
let requestNum = 0;
//
$http.requestStart = function(options) {
    if (options.load) {
        if (requestNum <= 0) {
            //
            uni.showLoading({
                title: '',
                mask: true
            });
        }
        requestNum += 1;
    }
    // 
    if (options.method == "FILE" && options.maxSize) {
        // : options.maxSize 
        let maxSize = options.maxSize;
        for (let item of options.files) {
            if (item.size > maxSize) {
                setTimeout(() => {
                    uni.showToast({
                        title: "",
                        icon: "none"
                    });
                }, 500);
                return false;
            }
        }
    }
	//token
	if (![$api.login].includes(options.url)) {
		// console.log('$storage',$storage.getStorage('APP_TOKEN'))
		if($storage.getStorage('APP_TOKEN')){
			// options.header['APP_TOKEN'] = $storage.getStorage('APP_TOKEN')
		}else{
			
		}
	}
    //token
    // options.header['token'] = "";
    return options; // return false 
}
//
$http.requestEnd = function(options) {
    //
    if (options.load) {
        requestNum = requestNum - 1;
        if (requestNum <= 0) {
            uni.hideLoading();
        }
    }
}
//
//
$http.dataFactory = async function(res) {
    // console.log("", {
    //     url: res.url,
    //     resolve: res.response,
    //     header: res.header,
    //     data: res.data,
    //     method: res.method,
    // });
    if (res.response.statusCode && res.response.statusCode == 200) {
        let httpData = res.response.data;
        if (typeof (httpData) == "string") {
            httpData = JSON.parse(httpData);
        }
        // ----------------------------------------------
        //
        if (httpData.success || httpData.code == 200) {  // ------
            // ------(then)------
            return Promise.resolve(httpData);
        } else {
            //
            if (res.isPrompt) { // 
                // uni.showToast({
                //     title: httpData.info || httpData.msg || httpData.message, // ------
                //     icon: "none",
                //     duration: 3000
                // });
            }
            // ------(catch)-------
            return Promise.reject({
                statusCode: 0,
                errMsg: (httpData.info || httpData.msg || httpData.message)
            });
        }
        // ----------------------------------------------
    } else {
        // (catch)
        return Promise.reject({
            statusCode: res.response.statusCode,
            errMsg: res.response.data.message
        });
    }
};
// 
$http.requestError = function(e) {
	console.log('',e);
	switch(e.statusCode){
		case 401:
		break;
		case 500:
		break;
		case 0:
			console.log('errMsg0',e);
			if(e.errMsg=='token'){
				// getWxCode().then(code=>{
				// 	$store.dispatch('userLogin',{code:code})
				// })
				return
			}
			uni.showToast({
				title: e.errMsg,
				icon: "none"
			});
			// throw e;
		break;
		default:
			uni.showToast({
				title: "",
				icon: "none"
			});
	}
}
export default $http